* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*background-color: rgb(140, 155, 181);*/
  background-color: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  cursor: pointer;
}

.div-grid {
  display: grid;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  gap: 5%;
  grid-template-columns: 70% 25%;
  grid-template-rows: auto auto auto;
  grid-template-areas:
    "div-recording div-settings"
    "div-upload-audio div-settings"
    "div-uploaded-sample div-uploaded-sample";
}

.container {
  border-top: 20px solid white;
  background-color: rgb(77, 92, 116);
  background-color: rgb(250, 250, 250);
  /*background-color: rgb(255, 255, 255);*/
  width: 80%;
  padding-top: 3%;
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.div-recording {
  grid-area: div-recording;
  background-color: gainsboro;
  background-color: rgb(255, 255, 255);
  padding-top: 3%;
  padding-bottom: 3%;
  width: 100%;
  /*margin: auto;*/
  display: flex;
  flex-wrap: wrap;
  border-radius: 5px;
  margin-bottom: 1%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.div-recording h1 {
  font-size: large;
  margin-right: auto;
  margin-left: 1.25%;
  margin-bottom: 1%;
}

.div-recording-buttons {
  display: flex;
  width: 100%;
  padding: 0%;
  justify-items: center;
  word-wrap: break-word;
}

.div-recording-buttons button {
  padding: 1%;
  margin: 1%;
}

.div-recording-buttons audio {
  margin-left: auto;
  margin-right: auto;
}

.div-upload-audio {
  grid-area: div-upload-audio;
  width: 100%;
}

.div-upload-audio button {
  padding: 1%;
}

.form-upload-audio {
  /*margin-left: auto;
  margin-right: auto;*/
  padding-top: 3%;
  padding-bottom: 3%;
  background-color: gainsboro;
  background-color: white;
  border-radius: 5px;
  min-height: 10%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.div-youtube-upload {
  display: flex;
}

.div-youtube-upload form {
  width: 100%;
  display: flex;
}

.div-youtube-upload form button {
  padding: 1%;
}

#input-youtubetext {
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  margin-right: 0.5%;
}

.div-settings {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-area: div-settings;
  background-color: red;
  width: 100%;
  border-radius: 5px;
  background-color: gainsboro;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.div-settings form {
}

.div-settings form div {
  display: flex;
  margin-left: 2%;
  padding: 1px;
}

.div-settings form div input {
  width: 7.5%;
}

.div-settings form input {
}

.div-settings form label {
}

.csv-download {
  margin-top: 1%;
  background-color: #f0f0f0; /* Green */
  border: 1px solid black;
  color: black;
  padding: 1% 2%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 2px;
  cursor: pointer;
}

.div-classification-results {
  margin-top: 5%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0%;
  border-top: 20px solid white;
}

.div-classification-results-small {
  margin-top: 5%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 0%;
  border-top: 20px solid white;
}

.div-chart {
  width: 100%;
}

.card {
  margin-top: 2%;
  margin-bottom: 2%;
  margin-left: 2%;
  margin-right: 2%;
  width: 40%;
  background-color: gainsboro;
  background-color: white;
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.card-small {
  margin-top: 2%;
  margin-bottom: 2%;
  margin-left: 2%;
  margin-right: 2%;
  width: 20%;
  background-color: gainsboro;
  background-color: white;
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.card-header {
  width: 90%;
  padding: 1%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  justify-items: center;
}

.card button {
  width: 30%;
}

.card-numbers {
  text-align: left;
  margin-left: 5%;
  margin-bottom: 1%;
}

.div-uploaded-sample {
  grid-area: div-uploaded-sample;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1%;
  padding-top: 2%;
  padding-bottom: 2%;
  background-color: gainsboro;
  background-color: white;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.div-uploaded-sample-hidden {
  visibility: hidden;
  grid-area: div-uploaded-sample;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1%;
  padding-top: 2%;
  padding-bottom: 2%;
  background-color: gainsboro;
  background-color: white;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 95%;
  }

  .div-recording {
    width: 95%;
  }

  .div-uploaded-sample {
    width: 95%;
  }
  .form-upload-audio {
    width: 95%;
  }
  .card {
    width: 100%;
  }
}
